@import "bootstrap"; 
@import "fonts";
@import "vars"; 
@import "buttons";
@import "form";   
@import "modal";  
@import "menu";  
@import "hacks"; 
@import "slick"; 
@import "hamburgers"; 



body{
	font-family: $f-o-r;
	font-weight: normal;
	font-size: 16px;
	line-height: 1.3;
	background-color: #fff;
	opacity: 0;
	transition: opacity .3s linear;
	color: $c-bck;
	&.loaded{
		opacity: 1;
	}
}  

.topp{
	padding: 20px 100px;
	&__blck{
		&-1{
			display: flex;
			flex-basis: 50%;
			align-items: center;
			justify-content: space-between;
		}
		&-2{
			display: flex;
			flex-basis: 50%;
			align-items: center;
			justify-content: flex-end;
		}
	}
	&__menu{
		list-style-type: none;
		font-size: 12px;
		font-family: $f-o-s;
		li{
			display: inline-block;
			margin-left: 30px;
		}
		a{
			color: $c-bck;
		}
	}
	&__soc{
		list-style-type: none;
		li{
			display: inline-block;
			margin: 5px;
		}
	}
	&__phone{
		font-size: 14px;	
		font-family: $f-o-e;
		color: $c-bck;
		margin: 0 15px;		
	}
	&__div{
		display: inline-block;
		font-size: 14px;
		margin-right: 10px;
	}
	&__sel{
		text-transform: uppercase;
		font-family: $f-o-s;
		font-size: 12px;
		position: relative;
		cursor: pointer;
		i{
			font-size: 10px;
			display: inline-block;
			margin-left: 5px;
		}
	}
	&__country{
		margin-left: 15px;
		cursor: pointer;
	}
}//.topp



.jumbo{
	&__txt{
		color: $c-wh;
		flex-basis: 60%;
	}
	&-1{
		min-height: 870px;
		align-items: flex-end;
		padding: 100px;
	}
	&__head1{
		font-size: 80px;
		font-family: $f-g-l;
		text-transform: uppercase;
		line-height: 1;
		margin-bottom: 20px;
	}
	&__head2{
		font-size: 50px;
		font-family: $f-g-l;
		// text-transform: uppercase;
		line-height: 1;
	}
	&__descr{
		font-size: 24px;
		font-family: $f-g-li;
		margin-top: 50px;
		border-left: 14px solid $c-yw;
		padding-left: 30px;
	}
}//.jumbo

.offer{
	.blck-1{
		flex-basis: 35%;
		display: flex;
		padding: 30px;
		justify-content: flex-end;
	}
	.blck-2{
		flex-basis: 65%;
		padding: 50px 120px 50px 50px;
	}
	&__head-1{
		flex-basis: 60%;
		font-size: 75px;
		font-family: $f-g-r;
		color: $c-wh;
	}
	&__head-2{
		font-size: 19;
		font-family: $f-o-b;
		margin-bottom: 10px;
		span{
			display: inline-block;
			width: 60px;
		}
	}
	&__txt{
		padding-left: 64px;
	}
	&__it{
		padding-left: 30px;
		padding-right: 30px;
	}
}//.offer

.easy{
	background-color: #F7F7F7;
}

@import "media";